/* Small reset CSS based on Eric Meyer */
.wrapper-vlite, .wrapper-vlite * {
        box-sizing: border-box;
    }
.wrapper-vlite div, .wrapper-vlite span, .wrapper-vlite p, .wrapper-vlite a, .wrapper-vlite ul, .wrapper-vlite li, .wrapper-vlite form, .wrapper-vlite label {
        margin: 0;
        padding: 0;
        border: 0;
        font-size: 100%;
        font: inherit;
        vertical-align: baseline;
    }
.wrapper-vlite body {
        line-height: 1;
    }
.wrapper-vlite ul {
        list-style: none;
    }
