.vlite-js{
    width: 100%;
    height: 100%;
}

.vl-wrapper-vlite{
    font-family: 'Roboto', arial, sans-serif;
    position: relative;
    height: 0;
    padding-top: 56.25%;
    background-color: #000;
    overflow: hidden;
}

.vl-wrapper-vlite::full-screen {
        width: 100%;
        height: 100%;
    }

/* Chrome fullscreen config */

.vl-wrapper-vlite:not(.vl-force-controls) video::-webkit-media-controls-enclosure {
            display: none !important;
        }

.vl-wrapper-vlite:not(.vl-force-controls) iframe.vlite-js{
            pointer-events: none;
        }

.vl-wrapper-vlite::-webkit-full-screen {
        width: 100%;
        height: 100%;
    }

/* Firefox fullscreen config */

::-moz-full-screen .vl-wrapper-vlite{
        width: 100%;
        height: 100%;
    }

.vl-wrapper-vlite::-moz-full-screen {
        width: 100%;
        height: 100%;
    }

.vl-wrapper-vlite.vl-fullscreen-display .vl-control-bar,
        .vl-wrapper-vlite.vl-fullscreen-display .vl-big-play-button{
            position: fixed;
            z-index: 2147483647;
        }

.vl-wrapper-vlite .vlite-js{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        -o-object-fit: cover;
           object-fit: cover;
    }

/* Skin */

.vl-wrapper-vlite.vl-playing .vl-poster,.vl-wrapper-vlite.vl-paused:not(.vl-first-start) .vl-poster{
            visibility: hidden;
            opacity: 0;
        }

.vl-wrapper-vlite.vl-playing .vl-big-play-button{
            visibility: hidden;
            opacity: 0;
        }

.vl-wrapper-vlite.vl-playing .vl-play-pause-button .vl-icon-pause{
                display: block;
            }

.vl-wrapper-vlite.vl-paused .vl-play-pause-button .vl-icon-play{
                display: block;
            }

.vl-wrapper-vlite.vl-paused .vl-control-bar.hidden{
            opacity: 1;
        }

.vl-wrapper-vlite .vl-overlay-video{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: undefined;
    }

.vl-wrapper-vlite .vl-overlay-video .vl-overlay-left,
        .vl-wrapper-vlite .vl-overlay-video .vl-overlay-right{
            position: absolute;
            top: 0;
            width: 30%;
            height: 100%;
        }

.vl-wrapper-vlite .vl-overlay-video .vl-overlay-left{
            left: 0;
        }

.vl-wrapper-vlite .vl-overlay-video .vl-overlay-right{
            right: 0;
        }

.vl-wrapper-vlite .vl-poster {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: #000;
        background-repeat: no-repeat;
        background-position: 50% 50%;
        background-size: cover;
        cursor: pointer;
        visibility: hidden;
        opacity: 0;
        z-index: 2;
    }

.vl-wrapper-vlite .vl-poster.vl-active{
            opacity: 1;
            visibility: visible;
        }