.vl-wrapper-vlite .vl-big-play-button {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 65px;
        height: 65px;
        -webkit-transform: translateX(-50%) translateY(-50%);
                transform: translateX(-50%) translateY(-50%);
        cursor: pointer;
        z-index: 2;
        -webkit-transition: opacity 200ms ease-in;
        transition: opacity 200ms ease-in;
        -webkit-transition-delay: 100ms;
                transition-delay: 100ms;
    }

        .vl-wrapper-vlite .vl-big-play-button .vl-icon-big-play {
            display: block;
            position: absolute;
            top: 50%;
            left: 50%;
            -webkit-transform: translateX(-50%) translateY(-50%);
                    transform: translateX(-50%) translateY(-50%);
        }

        .vl-wrapper-vlite .vl-big-play-button svg {
            fill: undefined;
            width: 60px;
            height: 60px;
        }

    .vl-wrapper-vlite .vl-control-bar{
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: undefined;
        padding-top: undefined;
        text-align: right;
        padding-left: undefined;
        padding-right: undefined;
        background-color: undefined;
        -webkit-transition: 250ms ease-in;
        transition: 250ms ease-in;
    }

    .vl-wrapper-vlite .vl-control-bar:hover{
            opacity: 1 !important;
        }

    .vl-wrapper-vlite .vl-control-bar:hover .vl-progress-bar{
                height: undefined;
            }

    .vl-wrapper-vlite .vl-control-bar.hidden{
            opacity: 0;
        }

    .vl-wrapper-vlite .vl-control-bar .vl-control-bar-inner{
            position: relative;
            width: 100%;
            height: 100%;
        }

    .vl-wrapper-vlite .vl-progress-bar{
        position: absolute;
        top: 0;
        left: undefined;
        right: undefined;
        height: undefined;
        background-color: rgba(0, 0, 0, 0.2);
        -webkit-transition: height 100ms ease-out;
        transition: height 100ms ease-out;
        z-index: 1;
    }

    .vl-wrapper-vlite .vl-progress-bar .vl-progress-input{
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            margin: 0;
            cursor: pointer;
            opacity: 0;
        }

    .vl-wrapper-vlite .vl-progress-bar .vl-progress-seek{
            position: absolute;
            top: 0;
            left: 0;
            width: 0;
            height: 100%;
            background-color: undefined;
            border-radius: 0px 4px 4px 0px;
            -webkit-transition: all 100ms ease-out;
            transition: all 100ms ease-out;
        }

    .vl-wrapper-vlite .vl-play-pause-button{
        position: relative;
        float: left;
        width: 40px;
        height: 100%;
        cursor: pointer;
    }

    .vl-wrapper-vlite .vl-play-pause-button .vl-player-icon {
            display: none;
            position: absolute;
            top: 50%;
            left: 50%;
            -webkit-transform: translateX(-50%) translateY(-50%);
                    transform: translateX(-50%) translateY(-50%);
        }

    .vl-wrapper-vlite .vl-play-pause-button svg {
            fill: undefined;
            width: 18px;
            height: 18px;
        }

    .vl-wrapper-vlite .vl-time{
        color: undefined;
        font-size: 13px;
        float: left;
        height: 100%;
        display: -webkit-box;
        display: flex;
        -webkit-box-align: center;
                align-items: center;
    }

    .vl-wrapper-vlite .vl-volume{
        position: relative;
        display: inline-block;
        width: 40px;
        height: 100%;
        cursor: pointer;
    }

    .vl-wrapper-vlite .vl-volume .vl-player-icon{
            display: none;
            position: absolute;
            top: 50%;
            left: 50%;
            -webkit-transform: translateX(-50%) translateY(-50%);
                    transform: translateX(-50%) translateY(-50%);
        }

    .vl-wrapper-vlite .vl-volume:not(.vl-muted) .vl-icon-volume-high{
                display: block;
            }

    .vl-wrapper-vlite .vl-volume.vl-muted .vl-icon-volume-mute{
                display: block;
            }

    .vl-wrapper-vlite .vl-volume svg {
            fill: undefined;
            width: 18px;
            height: 18px;
        }

    .vl-wrapper-vlite .vl-fullscreen{
        position: relative;
        display: inline-block;
        width: 40px;
        height: 100%;
        cursor: pointer;
    }

    .vl-wrapper-vlite .vl-fullscreen .vl-player-icon{
            display: none;
            position: absolute;
            top: 50%;
            left: 50%;
            -webkit-transform: translateX(-50%) translateY(-50%);
                    transform: translateX(-50%) translateY(-50%);
        }

    .vl-wrapper-vlite .vl-fullscreen:not(.vl-exit) .vl-icon-fullscreen{
                display: block;
            }

    .vl-wrapper-vlite .vl-fullscreen.vl-exit .vl-icon-shrink{
                display: block;
            }

    .vl-wrapper-vlite .vl-fullscreen svg {
            fill: undefined;
            width: 18px;
            height: 18px;
        }
