@-webkit-keyframes loader {
    0%, 80%, 100% {
        -webkit-transform: scale(0);
        transform: scale(0);
    } 40% {
        -webkit-transform: scale(1.0);
        transform: scale(1.0);
    }
}


@keyframes loader {
    0%, 80%, 100% {
        -webkit-transform: scale(0);
        transform: scale(0);
    } 40% {
        -webkit-transform: scale(1.0);
        transform: scale(1.0);
    }
}


.vl-wrapper-vlite.vl-loading .vl-wrapper-loader{
            opacity: 1;
            visibility: visible;
        }


.vl-wrapper-vlite.vl-loading .vl-big-play-button{
            opacity: 0;
        }


.vl-wrapper-vlite .vl-wrapper-loader{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        visibility: hidden;
        z-index: 5;
        display: -webkit-box;
        display: flex;
        -webkit-box-align: center;
                align-items: center;
        -webkit-transition: opacity 200ms ease-in, visibility 200ms ease-in;
        transition: opacity 200ms ease-in, visibility 200ms ease-in;
    }


.vl-wrapper-vlite .vl-wrapper-loader .vl-loader {
            margin: 0 auto;
            width: 70px;
            text-align: center;
        }


.vl-wrapper-vlite .vl-wrapper-loader .vl-loader > div {
                width: 18px;
                height: 18px;
                background-color: undefined;
                border-radius: 100%;
                display: inline-block;
                -webkit-animation: loader 1.4s infinite ease-in-out both;
                        animation: loader 1.4s infinite ease-in-out both;
            }


.vl-wrapper-vlite .vl-wrapper-loader .vl-loader .vl-loader-bounce-1 {
                -webkit-animation-delay: -0.32s;
                        animation-delay: -0.32s;
            }


.vl-wrapper-vlite .vl-wrapper-loader .vl-loader .vl-loader-bounce-2 {
                -webkit-animation-delay: -0.16s;
                        animation-delay: -0.16s;
            }
